import Taro from '@tarojs/taro-h5';
import * as tslib_1 from "tslib";
import Nerv from "nervjs";
import { Component } from "../../hoc/router";
import { View, Image, Text } from '@tarojs/components';
import { connect } from "@tarojs/redux-h5";
import Header from "../../components/header/index";
import ALink from '../../components/link';
import classNames from "classnames";
import * as actions from "../../actions/auth";
import * as utils from '../../libs/utils';
import { get } from "../../utils/request";
import './index.scss';
let User = class User extends Component {
  constructor() {
    super(...arguments);

    this.state = {
      currentData: [],
      user: {
        avatar_url: ""
      },
      showMenu: false,
      selectItem: 1
    };
  }
  componentDidMount() {
    this.getUser();
  }
  componentDidHide() {}
  getUser() {
    let loginname = this.$router.params.loginname;
    if (!loginname) {
      Taro.showToast({
        title: "缺少用户名参数"
      });
      Taro.navigateTo({
        url: "/pages/list/index"
      });
      return false;
    }
    get({
      url: "https://cnodejs.org/api/v1/user/" + loginname
    }).then(res => {
      let d = res.data;
      if (d && d.data) {
        let data = d.data;
        this.setState({
          user: data
        });
        if (data.recent_replies.length > 0) {
          this.setState({
            currentData: data.recent_replies
          });
        } else {
          this.setState({
            currentData: data.recent_topics,
            selectItem: 2
          });
        }
      }
    });
  }
  render() {
    const { selectItem, user, currentData } = this.state;
    const getLastTimeStr = (date, friendly) => {
      return utils.getLastTimeStr(date, friendly);
    };
    return <View className="flex-wrp">
        <Header pageType={"用户信息"} fixHead={true} showMenu={true} needAdd={true} />
        <View className="userinfo">
          <Image className="u-img" src={user.avatar_url} />
          <br />
          <Text className="u-name">{user.loginname}</Text>
          <View className="u-bottom">
            <Text className="u-time">
              {getLastTimeStr(user.create_at, false)}
            </Text>
            <Text className="u-score">
              积分：
              {user.score}
            </Text>
          </View>
        </View>
        <View className="topics">
          <View className="user-tabs">
            <View className={classNames({
            item: 1,
            br: 1,
            selected: selectItem === 1
          })} onClick={this.changeItem.bind(this, 1)}>
              最近回复
            </View>
            <View className={classNames({ item: 1, selected: selectItem === 2 })} onClick={this.changeItem.bind(this, 2)}>
              最新发布
            </View>
          </View>
          {currentData.map(item => {
          return <View className="message">
                <View className="user">
                  <ALink link-class="head" to={{ url: "/pages/user/index", params: { loginname: item.author.loginname } }}>
                    <Image class="head" src={item.author.avatar_url} />
                  </ALink>
                <ALink link-class="info" to={{ url: "/pages/topic/index", params: { id: item.id } }}>
                    <View className="t-title">{item.title}</View>
                    <Text className="cl mt12">
                      <Text className="name">
                        {item.author.loginname}
                      </Text>
                    </Text>
                    <Text className="cr mt12">
                      <Text className="name">
                        {getLastTimeStr(item.last_reply_at, true)}
                      </Text>
                    </Text>
                  </ALink>
                </View>
              </View>;
        })}
          {currentData.length === 0 ? <View className="no-data">
              <Text className="iconfont icon-empty"></Text>
              暂无数据!
            </View> : ""}
        </View>
      </View>;
  }
  config = {
    navigationBarTitleText: "用户信息"
  };
  changeItem = idx => {
    const currentData = idx === 1 ? this.state.user.recent_replies : this.state.user.recent_topics;
    this.setState(prevState => ({
      ...prevState,
      selectItem: idx,
      currentData: currentData
    }));
  };
};
User = tslib_1.__decorate([connect(({ auth }) => ({
  userInfo: auth
}), dispatch => ({
  authCheckState() {
    dispatch(actions.authCheckState());
  }
}))], User);
export default User;